﻿/*
 * @namespace Browser
 * @aka Q3D.Browser
 *
 * 本命名空间提供静态属性用于浏览器及其特征的检测
 *
 * @example
 *
 * ```js
 * if (Q3D.Browser.ielt9) {
 *   alert('请升级浏览器版本!');
 * }
 * ```
 */

(function () {

	var ua = navigator.userAgent.toLowerCase(),
	    //doc = document.documentElement,

	    ie = 'ActiveXObject' in window,

	    webkit    = ua.indexOf('webkit') !== -1,
	    //phantomjs = ua.indexOf('phantom') !== -1,
	    chrome    = ua.indexOf('chrome') !== -1,
	    gecko     = ua.indexOf('gecko') !== -1  && !webkit && !window.opera && !ie,

	    win = navigator.platform.indexOf('Win') === 0,

	    msPointer = !window.PointerEvent && window.MSPointerEvent,
	    pointer = window.PointerEvent || msPointer;


	var touch = !window.L_NO_TOUCH && (pointer || 'ontouchstart' in window ||
			(window.DocumentTouch && document instanceof window.DocumentTouch));

	Q3D.Browser = {

		// @property ie: Boolean
		//  判断是否 I E 浏览器（不包括 Edge）
		ie: ie,

		// @property ielt9: Boolean
		// `true`  表示 IE 浏览器版本 < 9
        ielt9: ie && !document.addEventListener,
        
        // @property ie11: Boolean
		// `true`  表示 IE 11
		ie11: ie && !document.attachEvent,

		// @property edge: Boolean
		// `true`  表示 Edge 浏览器
		edge: 'msLaunchUri' in navigator && !('documentMode' in document),

		// @property webkit: Boolean
		// `true`  表示基于 webkit 内核浏览器，如 Chrome 和 Safari （包括移动版本）
		webkit: webkit,

		// @property gecko: Boolean
		// `true`  表示基于 gecko 内核浏览器， 如 Firefox
		gecko: gecko,
		
		// @property chrome: Boolean
		// `true`  表示 Chrome 浏览器
		chrome: chrome,

		// @property safari: Boolean
		// `true`  表示 Safari 浏览器
		safari: !chrome && ua.indexOf('safari') !== -1,

		// @property win: Boolean
		// `true`  表示运行在 Windows 平台上的浏览器
		win: win,

	// @property touch: Boolean
		// `true`  表示所有支持触屏事件的浏览器
		touch: !!touch,

		// 
		// `true` for browsers implementing the Microsoft touch events model (notably IE10).
		msPointer: !!msPointer,

		// 
		// `true` for all browsers supporting [pointer events](https://msdn.microsoft.com/en-us/library/dn433244%28v=vs.85%29.aspx).
		pointer: !!pointer
	};

}());
